/* Thim Testimoniol */

.thim-testimonial-slider {
  background-size: cover;
  position: relative;
  overflow: hidden;
  padding-top: 95px;
  padding-bottom: 84px;
  text-align: center;
  .thim-sc-heading .sc-heading{
    .heading_primary {
      color: #fff;
      i {
        color: #fff;
      }
    }
  }
  > * {
    position: relative;
    z-index: 1;
  }
  > .icon-quote {
    background-image: url("assets/images/hairsalon-sprites.png");
    background-position: 0px -108px;
    background-repeat: no-repeat;
    width: 36px;
    height: 36px;
    display: block;
    margin: 60px auto 11px;
  }

  &:after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: .9;
  }

  &.no-mask {
    &:after {
      @include opacity(0);
    }
  }
}

.thim-content-slider {
  position: relative;
  max-width: 750px;
  margin: auto;
  .slides-wrapper {
    .scrollable {
      list-style: none;
      margin: -10px 0;
      padding: 0;
      position: relative;
      overflow: hidden;
      display: block;
      margin: 0 auto;
      height: 200px !important;
      > li {
        position: absolute;
        margin-top: 85px;
        .slide-content {
          margin: 10px;
          cursor: pointer;
        }
        .around-img {
          position: relative;
          &:before {
            position: absolute;
            top:0;
            left:0;
            right: 0;
            bottom: 0;
            height: 80px;
            width: 80px;
            display: inline-block;
            content: '';
            @include opacity(.4);
            @include rounded(50%);
            border: 5px solid #fff;
          }
        }

        img {
          line-height: 1;
          vertical-align: middle;
          cursor: pointer;
          @include rounded(50%);
          min-width: 80px;
        }
        &.mid-item {
          z-index: 15;
          cursor: auto;
          .thumb-content {
            @include opacity(1);
            position: absolute;
            display: block;
            border: 0;
            width: 700px;
            text-align: center;
            @include translate(-50%, -69px);
            left: 50%;
          }
          .around-img {
            &:before {
              position: absolute;
              top:-1px;
              left:-1px;
              right: -1px;
              bottom: -1px;
              height: 82px;
              width: 82px;
              @include opacity(1);
            }
          }

          .slide-content {
            &:before {
              @include opacity(0);
            }
          }
        }

        .thumb-content {
          @include opacity(0);
          @include transition();
          display: none;
          h4 {
            font-size: 15px;
            color: #FFF;
            font-weight: bold;
            line-height: 22px;
            margin-bottom: 0;
            margin-top: 13px;
          }
          > div {
            color: #FFF;
            font-size: 13px;
            font-weight: 600;
            line-height: 22px;
          }
        }
      }
    }

  }

  .slides-content {
    color: #FFF;
    font-size: 22px;
    line-height: 36px;
    margin: auto;
    font-family: Playfair Display;
    font-style: italic;
    > div {
      float: left;
      width: 100%;
      margin-right: -100%;
      text-align: center;
      margin-top: 30px;
      opacity: 0;
      font-size: 24px;
    }
    &:after {
      display: block;
      content: '';
      clear: both;
    }
  }
}

.thim-testimonial-slider.border-white .thim-content-slider .slides-wrapper .scrollable>li {
  &.mid-item div.around-img:before {
    border-color: #fff;
  }
}
